import { Component, OnInit } from '@angular/core';
import { ToastController, ActionSheetController, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-registrasidusun',
  templateUrl: './registrasidusun.page.html',
  styleUrls: ['./registrasidusun.page.scss'],
  providers: [ApiService]
})
export class RegistrasidusunPage implements OnInit {

  subscribe: any;
  items = [];
  progressloading: any;

  constructor(
    public NavController: NavController,
    private apiService: ApiService,
    public alertController: AlertController,
    public loadingController: LoadingController,
    public toastController: ToastController,
    public actionSheetController: ActionSheetController
  ) { }

  ngOnInit() {
  }

  ionViewDidEnter() {
    this.getListDusun();
  }

  async presentProgressLoading(message: string) {
    this.progressloading = await this.loadingController.create({
      cssClass: 'my-custom-class',
      message: message,
      mode: 'ios',
      duration: 3000
    });
    return this.progressloading.present();
  }

  async presentToast(color, header, message, duration) {
    const toast = await this.toastController.create({
      header: header, //"Warning",
      message: message, //"Mohon isi semua formulir",
      color: color, //"danger",
      mode: "ios",
      position: "middle",
      duration: duration, // 3000
    });
    toast.present();
  }

  async openOptionDusun(item: any) {

    let buttons = [
      {
        text: 'Edit Informasi Dusun',
        handler: async () => {
          item.action = 'edit';
          this.NavController.navigateForward(['/basicdata/informasidusun', item]);
        }
      },
      {
        text: 'Edit Data Dasar Dusun',
        handler: async () => {
          this.NavController.navigateForward(['/basicdata/datadasardusun', item]);
        }
      },
      {
        text: 'Tutup',
        role: 'cancel',
        handler: () => {
          // console.log('Cancel clicked');
        }
      }
    ];


    const actionSheet = await this.actionSheetController.create({
      header: item.name, //'Albums',
      cssClass: 'my-custom-class',
      mode: 'ios',
      buttons: buttons
    });
    await actionSheet.present();
  }

  createDusun() {
    this.NavController.navigateForward(['/basicdata/informasidusun', { action: 'create' }]);
  }


  getListDusun() {
    this.presentProgressLoading('Loading...');
    this.items = [];
    let geo_id = localStorage.getItem('geo_id');
    this.subscribe = this.apiService.get('user_areas?no_expand=user,geo&geo_id=' + geo_id, {}).subscribe(
      data => {
        // this.catecories = data.List;
        this.items = data;
        // console.log(data);

      }, error => {
        this.presentToast('danger', 'Gagal', 'Gagal mendapatkan data, cobalah beberapa saat lagi', 2000);

      }
    );
  }

  goBack() {
    this.NavController.navigateBack(['/basicdata'], { replaceUrl: true });
  }

}
