import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ToastController, IonRouterOutlet, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-checklist',
  templateUrl: './checklist.page.html',
  styleUrls: ['./checklist.page.scss'],
  providers: [ApiService]
})
export class ChecklistPage implements OnInit {

  params: any;
  catecories = [];
  subscribe: any;
  arrayInput = [];


  constructor(
    private route: ActivatedRoute,
    public NavController: NavController,
    private apiService: ApiService,
    public alertController: AlertController,
    public toastController: ToastController,
  ) {
    this.params = this.route.snapshot.params;
  }

  ngOnInit() {
    this.getListItems();
  }

  async presentAlertOpenChecklish(category) {

    let header = this.params.name;
    let message = category.name;


    this.arrayInput = [
      {
        name: 'radio1',
        type: 'radio',
        label: 'Perlu dikerjakan',
        value: '0',
        checked: true
      },
      {
        name: 'radio2',
        type: 'radio',
        label: 'Sedang dikerjakan',
        value: '1'
      },
      {
        name: 'radio3',
        type: 'radio',
        label: 'Sudah dikerjakan',
        value: '2'
      }
    ];

    if (category.status != null) {
      this.arrayInput.forEach((v) => {
        let checked = (v.value == category.status) ? true : false;
        v.checked = checked;
      });
    } else {
      this.arrayInput[0].checked = true;
    }

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: header,
      message: message,
      mode: "ios",
      backdropDismiss: true,
      inputs: this.arrayInput,
      buttons: [
        {
          text: 'Baca Pedoman',
          handler: data => {
            this.presentAlertBacaPedoman(category);
          }
        },
        {
          text: 'Simpan',
          handler: data => {
            this.updateStatus(category.checklist_item_id, data);
          }
        }
      ]
    });

    await alert.present();
  }

  async presentAlertBacaPedoman(category) {


    let header = 'Pedoman';
    let message = category.guidelines_text;

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: header,
      message: message,
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Tutup',
          handler: data => {
            this.presentAlertOpenChecklish(category);
          }
        }
      ]
    });

    await alert.present();
  }

  async presentToast(color, header, message) {
    const toast = await this.toastController.create({
      header: header, //"Warning",
      message: message, //"Mohon isi semua formulir",
      color: color, //"danger",
      mode: "ios",
      position: "middle",
      duration: 3000
    });
    toast.present();
  }

  updateStatus(id, status) {
    let params = { status: status };
    this.subscribe = this.apiService.put('checklist_items/' + id, params).subscribe(
      data => {
        this.getListItems();
      },
      error => {
        this.presentToast('danger', 'Gagal Update Data', 'Mohono Hubungi Pihak Terkait');
      }
    );
  }

  getListItems() {

    this.catecories = [];
    this.subscribe = this.apiService.get('checklist_categories?language_id=2&id=' + this.params.id, {}).subscribe(
      data => {
        this.catecories = data.List;
      }
    );

  }

  goBack() {
    this.NavController.navigateBack(['/basicdata'], { replaceUrl: true });
  }

}
