import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BasicdataPage } from './basicdata.page';

import { ChecklistPage } from './checklist/checklist.page';
import { BasicsettingPage } from './basicsetting/basicsetting.page';
import { RegistrasidusunPage } from './registrasidusun/registrasidusun.page';
import { InformasidusunPage } from './informasidusun/informasidusun.page';
import { DatadasardusunPage } from './datadasardusun/datadasardusun.page';

const routes: Routes = [
  {
    path: '',
    component: BasicdataPage
  },
  {
    path: 'checklist',
    component: ChecklistPage
  },
  {
    path: 'basicsetting',
    component: BasicsettingPage
  },
  {
    path: 'registrasidusun',
    component: RegistrasidusunPage
  },
  {
    path: 'informasidusun',
    component: InformasidusunPage
  },
  {
    path: 'datadasardusun',
    component: DatadasardusunPage
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BasicdataPageRoutingModule { }
