import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Platform, IonRouterOutlet, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-basicdata',
  templateUrl: './basicdata.page.html',
  styleUrls: ['./basicdata.page.scss'],
  providers: [ApiService]
})
export class BasicdataPage implements OnInit {

  catecories = [];
  subscribe: any;
  userID: string;

  isDataDasarChecked = false;
  isDataRegistrasiDusunChecked = false;

  constructor(
    public NavController: NavController,
    private apiService: ApiService,
    public alertController: AlertController,
    private router: Router,
  ) { }

  ngOnInit() {

    let netwotkInformation = localStorage.getItem('netwotkInformation');
    if (netwotkInformation == 'offline') {
      this.alertNeedInternet();
    } else {
      let currentUser = localStorage.getItem('currentUser');
      let d = JSON.parse(currentUser);
      this.userID = d.user.id;
      this.getListCategory();
    }


  }

  ionViewDidEnter() {
    this.getDetailUser();
    this.getListDusun();
  }

  async alertNeedInternet() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Halaman ini membutuhkan koneksi internet!',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Kembali',
          handler: () => {
            this.goBack();
          }
        }
      ]
    });

    await alert.present();
  }

  async alertToRelogin() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Akun anda terdeteksi sedang digunakan diperangkat lain, mohon login kembali untuk mendapatkan akses Anda kembali',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Login Ulang',
          handler: () => {

            localStorage.removeItem('currentUser');
            localStorage.removeItem('geo_id');
            localStorage.removeItem('api_key');
            this.router.navigate(['front/']);

          }
        }
      ]
    });

    await alert.present();
  }

  getDetailUser() {
    // this.catecories = [];
    this.subscribe = this.apiService.get('users/' + this.userID + '?no_expand=user_areas', {}).subscribe(
      data => {
        // this.catecories = data.List;
        let usr = data.user;
        this.isDataDasarChecked = (usr.ts_updated != '' && usr.ts_updated != null)
          ? true : false;

      },
      error => {
        if (error.status == 401) {
          this.alertToRelogin();
        }
      }
    );
  }

  getListDusun() {
    let geo_id = localStorage.getItem('geo_id');
    this.subscribe = this.apiService.get('user_areas?no_expand=user,geo&geo_id=' + geo_id, {}).subscribe(
      data => {
        this.isDataRegistrasiDusunChecked = (data.length > 0) ? true : false;
        // console.log(data);
      }
    );
  }

  getListCategory() {

    this.catecories = [];
    this.subscribe = this.apiService.get('checklist_categories?language_id=2', {}).subscribe(
      data => {
        this.catecories = data.List;
        // console.log(this.catecories);
      }
    );

  }



  actionGetCheclist(params) {
    this.NavController.navigateForward(['/basicdata/checklist', params]);
  }

  gotoPage(path: string) {
    this.NavController.navigateForward([path]);
  }

  goBack() {
    this.NavController.navigateBack(['/home'], { replaceUrl: true });
  }

}
